<template>
  <div class="help">
    <vx-header :showback="1" :title="$t('settings.help')"></vx-header>
    <div class="vx-container">
      <div class="help_cont">
        <vx-cell :title="$t('settings.operationManual')" @link="open"></vx-cell>
        <vx-cell :title="$t('settings.softwareVersion')" :showLink="false" :value="version"></vx-cell>
        <vx-cell :title="$t('settings.hardwareVersion')" @link="$router.push({name:'versions'})"></vx-cell>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        version: window.Android.getVersionName()
      };
    },
    methods: {
      open() {
        window.Android.openOperation();
      }
    }
  };
</script>

<style lang="less">
  .help {
      .help_cont {
          margin-top: 25px;
      }
      .vc-cell {
          border-top: 1px solid #4f5054; /*no*/
          &-value {
              font-size: 12px; /*no*/
          }

          &:first-child {
              border-top: none;
          }
      }
  }
</style>



